
html {
  background-image: url(./background.jpg);
  background-repeat: no-repeat;
  background-size: auto;
  font-family: 'HelveticaNeue', sans-serif;
  color: #fff;
  display: flex;
}

.coinname {
  font-size: 3vw;


}

.nav {
  padding:20px;
}

.title  {
  margin: auto;
  color: #26ff6b;
  font-size: 2vw;


}

.row {
  display: flex;
  width: 100%;
  margin: auto;
}

.column {
  flex: 50%;
}


@media (max-width: 800px) {
  .row {
    display: inline-block;
    height: 100%;
  }
}






.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  width: 30vmin;
  pointer-events: none;
}

.logo {
  width: 100%;

}


@media (prefers-reduced-motion: no-preference) {
  .App-logo2 {
    animation: App-logo-spin infinite 20s linear;
  }
}


.box {
  color: #fff;
  width: 50vh;
  height: 20vh;
  margin: auto;
  padding: .25vh;
  display: inline-block;
  }

 .box h2 {
  margin: 0px;
  font-size: 2vh;
  text-align: center;

 }

 .box h3 {
  margin: 0px;
 }

 .box h4 {
   font-size: 1.5vh;

 }

 .burn {
  display: inline-block;
  padding: .25vh;
 }



button {
  padding: .25vh;
  background-color:transparent;
  border-radius:.5vh;
  border:0px solid #301934;
  display:inline-block;
  font-size: 4vh;
  font-weight:bold;
  font-family: 'HelveticaNeue', sans-serif;


 }
 .glow {
   color: #fff;
   font-size: 5vw;
   text-align: center;
   -webkit-animation: glow 1s ease-in-out infinite alternate;
   -moz-animation: glow 1s ease-in-out infinite alternate;
   animation: glow 1s ease-in-out infinite alternate;
 }

 .glow2 {
   color: #dd4428;
   font-size: 3vw;
   text-align: center;
   -webkit-animation: glow 1s ease-in-out infinite alternate;
   -moz-animation: glow 1s ease-in-out infinite alternate;
   animation: glow 1s ease-in-out infinite alternate;
 }





 @keyframes glow {
   from {
     text-shadow: 0 0 6px #dd4428, 0 0 4px rgb(39, 135, 170), 0 0 6px #5B2282;
     border-shadow: 0 0 2px #dd4428, 0 0 4px rgb(39, 135, 170), 0 0 6px #5B2282;
   }
   to {
     text-shadow: 0 0 60px #dd4428, 0 0 4px #2E1A47, 0 0 2px #653574;
     border-shadow: 0 0 6px #dd4428, 0 0 4px rgb(39, 135, 170), 0 0 6px #653574;
   }
 }

 @keyframes glow2 {
   from {
     text-shadow: 0 0 2px #4A003D, 0 0 4px rgb(39, 135, 170), 0 0 6px #5B2282;
     border-shadow: 0 0 2px #4A003D, 0 0 4px rgb(39, 135, 170), 0 0 6px #5B2282;
   }
   to {
     text-shadow: 0 0 6px #4A203B, 0 0 4px #2E1A47, 0 0 2px #653574;
     border-shadow: 0 0 2px #4A003D, 0 0 4px rgb(39, 135, 170), 0 0 6px #653574;
   }
 }

.shadow {
  font-size: 15px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.201);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;

}

.shadow:hover {
  background-color: red;
  box-shadow: 0px 2px 3px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-1px);
}

.numbers {
    color: #fff;
}
.social-icons {
  padding: 1vw;
}
